import React from 'react';
import { Contact } from '../../api/contacts';
import './ContactsList.scss';

interface Props {
  contacts: Contact[];
  onSelectContactID(userId: number) : void;
  selectID: number;
  deleteContact(userId: number) : void;
}

interface State {
  selectValue?: string;
}

export class ContactsList extends React.Component<Props, State> {
  state: State = {
    selectValue: 'all',
  };

  render() {
    const {
      contacts,
      onSelectContactID,
      selectID,
      deleteContact,
    } = this.props;
    const { selectValue } = this.state;

    return (
      <div className="ContactsList">
        <h2 className="ContactsList__title">
          Contacts:
        </h2>

        <div className="ContactsList__list-container">
          <ul className="ContactsList__list">
            {contacts.map((contact: Contact) => {
              return (
                <li
                  key={contact.id}
                  className="ContactsList__item"
                  onClick={() => {
                    onSelectContactID(contact.id);
                  }}
                  data-title="view the contact"
                >
                  <label className="ContactsList__label">
                    {contact.name}
                  </label>

                  <div className="ContactsList__container">
                    <label className="ContactsList__label">
                      {contact.phone}
                    </label>

                    <button
                      type="button"
                      className="ContactsList__button"
                      onClick={() => {
                        deleteContact(contact.id);
                      }}
                    >
                      Delete
                    </button>
                  </div>
                </li>
              );
            })}
          </ul>
        </div>
      </div>
    );
  }
}
